import React, { useEffect, useState } from 'react';
import {Button, Spinner, Modal} from "react-bootstrap";
import { toast } from 'react-toastify'; //toster
import Web3 from 'web3';

import ETHBulkContract from '../contracts/ETHBulkContract';
import BSCBulkContract from '../contracts/BSCBulkContract';

import ETHimage from '../assects/ethereum.svg'
import BSCimage from '../assects/binance.svg'
import DownArrow from '../assects/down-arrow.svg'

let web3;

const  ConnectWallet=(props)=> {
  const [loading,setLoading] = useState(false);//Loading bar
  const [modalShow, setModalShow] = React.useState(false);

  useEffect(()=>{
    if(window.ethereum) {
      window.ethereum.on('chainChanged', () => {
        window.location.reload();
      })
      window.ethereum.on('accountsChanged', function (accounts) {
        // Time to reload your interface with accounts[0]!
        connect();
      })
    }
    //already connected   //window.ethereum.isConnected()
    if(!props.walletData.isConnected){
      if(sessionStorage.getItem('walletConneted')=="true"){
        connect();
      }
    }
  },[])

    async function connect(){
      setLoading(true)
      //connect to web3
      if(typeof window!=='undefined' && typeof window.web3 !== 'undefined'){
        //we are in the browser and metamask is running
          //ETH test net 3,  // mainnet 1
          // BSC test net 97  //mainnet 56
        web3 = new Web3(window.web3.currentProvider);
        let netId = await web3.eth.net.getId();
        var networkName="";
        console.log(netId);
        switch (netId) {
          case 1:
            networkName='Eth'
            console.log('Network is Ethereum mainnet')
            break
          case 56:
            networkName='BSC'
            console.log('Network is BSC mainnet')
            break
          // case 3:
          //   networkName='Eth'
          //   console.log('Network is Ethereum Ropstern test network')
          //   break
          // case 97:
          //   networkName='BSC'
          //   console.log('Network is BSC mainnet')
          //   break
          default:
            toast.error('Please select mainnet first')
            setLoading(false)
            return;
        }
        //set details
        //log network
        // web3.eth.net.getNetworkType().then(console.log);
        let accounts;
        try{
          accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
        } catch(error){
          if(error.code===4001){
            toast.error("Please allow metamask to connect")
            }
            setLoading(false)
            return;
          // console.log(error)
        }
        // console.log(accounts)
        if(accounts.length<1){
          setLoading(false)
          toast.error('Please unlock metamask wallet first!')
          return;
        }
        let weiBalance= await web3.eth.getBalance(accounts[0]);
        let balance=truncate(web3.utils.fromWei(weiBalance, 'ether'),4);
        // console.log(balance)
        // console.log('Checking tier');
        var token=0,tokenEth=0,tokenBSC=0;

        let errorCall=false
        do {
          try{
          tokenEth = await ETHBulkContract.methods.balanceOf(accounts[0]).call();//0x68fe5fcccc6504fae59f4b8a52ea06707cfd088e
          tokenBSC = await BSCBulkContract.methods.balanceOf(accounts[0]).call();
          // console.log("You have\n TokenEth "+tokenEth+"\nTokenBSC "+ tokenBSC);
          tokenEth=web3.utils.fromWei(tokenEth, 'ether');
          tokenBSC=web3.utils.fromWei(tokenBSC, 'ether');
          token = parseFloat(tokenEth) + parseFloat(tokenBSC);
          console.log("You have\n TokenEth "+tokenEth+"\nTokenBSC "+ tokenBSC);
          errorCall=false
          }catch(error){
            console.log(error)
            errorCall=true
          }
        } while(errorCall)  //handle network issue while call
        
        let tier = 0
        if(token>=1000000){
          tier=1
        } else if(token>=500000){
          tier=2
        } else if(token>=100000){
          tier=3
        } else if(token>=10000){
          tier=4
        }
        props.setWalletData({walletAddress:accounts[0],balance:balance,isConnected:true,networkName:networkName,ETHTokenCount:parseFloat(tokenEth),
        BSCTokenCount:parseFloat(tokenBSC),TotalTokenCount:token,tier:tier,web3:web3})
        sessionStorage.setItem('walletConneted',true)
      } else{
        //on  the browser or user is not running metamask
        toast.error("Metamask wallet not found! Please make sure wallet is installed and running!");
      }
      setLoading(false)
    }

    async function disconnect(){
      setLoading(true)
      //Disconnect
      sessionStorage.setItem('walletConneted',false)
      props.setWalletData({walletAddress:'',balance:0,isConnected:false,networkName:'',ETHTokenCount:0,
      BSCTokenCount:0,TotalTokenCount:0,tier:0,web3:null})
      setLoading(false)
    }

    //Show balance with 4 decimal places
    function truncate(str, maxDecimalDigits) {
        if (str.includes('.')) {
            const parts = str.split('.');
            return parts[0] + '.' + parts[1].slice(0, maxDecimalDigits);
        }
        return str;
    }
    
    function DetaislModel(props) {
      return (
        <Modal
          {...props}
          size="md"
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header>
            <Modal.Title id="contained-modal-title-vcenter">
              Wallet Details
            </Modal.Title>
            <button type="button" style={{backgroundColor:'white'}} onClick={props.onHide}><span style={{fontSize:'25px'}}></span></button>
          </Modal.Header>
          <Modal.Body>
            <p>
              <b>Address:</b> {props.walletData.walletAddress}
            </p>
            <p>
              <b>Balance:</b> {props.walletData.balance}&ensp;{props.walletData.networkName}
            </p>
          </Modal.Body>
          <Modal.Footer>
            <Button style={{borderRadius:'25px',marginLeft:'5px'}}  variant="secondary" onClick={disconnect}>Disconnect</Button>
          </Modal.Footer>
        </Modal>
      );
    }

    return (
      <>
      {
        loading?(
            <Spinner animation="border" role="status">
              <span className="visually-hidden">Loading...</span>
            </Spinner>
        ):(<>
          { props.walletData.isConnected?(
            <>
            <span class="bg-wallet" onClick={() => setModalShow(true)}>
              {props.walletData.networkName=="Eth"?(<img height="30pts" src={ETHimage}/>):(<img height="30pts" src={BSCimage}/>)}
              &ensp;{props.walletData.balance}&ensp;{props.walletData.networkName} &emsp;
              Address : {props.walletData.walletAddress.substr(0,5)}...{props.walletData.walletAddress.slice(-3)}
              &ensp; <img height="22pts" src={DownArrow}/>
            </span>
              <DetaislModel walletData={props.walletData}
                  show={modalShow}
                  onHide={() => setModalShow(false)}
                />
            </>
          ):(
            <Button  className="button-strict" onClick={connect}>Connect Wallet</Button>        
          )}</>
        )
      }
      </>
    );
  }
  
  export default ConnectWallet
  