import React from "react";
import {Button,Modal} from "react-bootstrap";


function NoAccessModel(props) {
    return (
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header>
          <Modal.Title id="contained-modal-title-vcenter">
            No Access
          </Modal.Title>
            <button type="button" style={{backgroundColor:'white'}} onClick={props.onHide}><span style={{fontSize:'25px'}}></span></button>
        </Modal.Header>
        <Modal.Body>
          <div style={{textAlign:'center'}}>
            <p>
            Due to confidentiality reasons, the vast majority of our deals are not posted in the public All Deals area. These deals are only accessible to our members in the Member Area.
            Higher Tiers grant larger individual allocations and provide access to more exclusive deals that might not be accessible in lower Tiers.
            </p>
            <br/>
            <p>
              To access Member Area you need to lock the following number of tokens:
            </p>
            <p>
              Top 10 - 10 users with highest locked balances
            </p>
            <p>
              Tier 1 - 1 000 000 BULK
            </p>
            <p>
              Tier 2 - 500 000 BULK
            </p>
            <p>
              Tier 3 - 100 000 BULK
            </p>
            <p>
              Tier 4 - 10 000 BULK
            </p>
          </div>
          <div className="col-100">
              <br/>
              <a href="https://app.uniswap.org/#/swap?outputCurrency=0xa143ac515dca260a46c742c7251ef3b268639593" target="_blank"
              style={{float:"left",marginLeft:"10%"}}>
                  <Button className="uniswap"> Buy on Uniswap </Button>
              </a>  
              <a href="https://pancakeswap.finance/swap?outputCurrency=0xa143ac515dca260a46c742c7251ef3b268639593" target="_blank"
              style={{float:"right",marginRight:"10%"}}>
                  <Button className="pancakeswap">Buy on Pancakeswap</Button>
              </a>
              <br/>
          </div>  
          <div className="col-100" style={{textAlign:'center'}}>
              <a href="https://bulklock.web.app/" target="_blank">
                  <Button>Lock Tokens</Button>
              </a>
          </div>
        </Modal.Body>
        {/* <Modal.Footer>
          <Button onClick={props.onHide}>Close</Button>
        </Modal.Footer> */}
      </Modal>
    );
  }

  export default NoAccessModel