import React from "react";
import './navigationBar.css'
// Route to other page 
import { useLocation, useHistory } from "react-router-dom";
import { useEffect } from "react";

const Menubar=()=>{
    // jump to other page
    const history = useHistory()

    const location = useLocation()

    useEffect(()=>{
        // console.log(location.pathname.startsWith("/member-area/"))
        if(location.pathname.startsWith("/member-area/")){
            document.getElementById("check01").checked=true;
        }
    })
    
    return(
        <>
        <div className="left-nav">
            <ul id="menu">
                <li><a className="menuItmes" onClick={e=>history.push('/')}>Public Area</a></li>
                <li><a className="menuItmes" onClick={e=>history.push('/my-deals')}>My Deals</a></li>
                <li> <input id="check01" type="checkbox" name="menu"/>
                <label for="check01">Members Area</label>
                    <ul class="submenu">
                    <li><a className="menuItmes" onClick={e=>history.push('/member-area/top10')}>Top 10</a></li>
                    <li><a className="menuItmes" onClick={e=>history.push('/member-area/tier1')}>Tier 1</a></li>
                    <li><a className="menuItmes" onClick={e=>history.push('/member-area/tier2')}>Tier 2</a></li>
                    <li><a className="menuItmes" onClick={e=>history.push('/member-area/tier3')}>Tier 3</a></li>
                    <li><a className="menuItmes" onClick={e=>history.push('/member-area/tier4')}>Tier 4</a></li>
                    </ul>
                </li> 
                <li><a style={{marginTop:'5px'}} className="menuItmes" onClick={e=>history.push('/profile')}>My Profile</a></li>
            </ul>

        </div>
        </>
    )
}
  export default Menubar