import React, { useState, useEffect, useContext } from "react";
import {Container, Row, Col, Button,Spinner} from "react-bootstrap";
import { toast } from 'react-toastify'; //toster
import './Style.css';
import Menubar from "../components/pageComponents/navigationBar";
import {WalletContext} from './WalletContext';
//connect wallet button
import ConnectWallet from "../components/Wallet/connectWallet";
// Route to contribute page 
import { useHistory } from "react-router-dom";
//utils function 
import { isFinished, isActive, isUpcoming, getDealStauts } from "./utils";
//sample data
// import {result} from "./data";


const MyDeals = ()=>{  
    const [dealsData,setDealsData] = useState([{}]);
    const [loading,setLoading] = useState(false);//Loading bar
    //import hooks for object
    const [walletData, setWalletData] =  useContext(WalletContext)

    // jump to contribute page
    const history = useHistory()

    const getDeals = async ()=>{
        if(!walletData.isConnected){
            toast.error('Please Connect wallet first!')
        }
        let dealsList;
        //API call for check user is registered
        let userData={
            userWalletAddress:walletData.walletAddress
        }
        console.log(userData)
        await fetch('https://ido-api-services.herokuapp.com/get-deals-by-walletAddress', {
            method: 'post',
            headers: { 'Content-Type': 'application/json' },
            cache:"no-cache",
            body:  JSON.stringify( userData ),
        }).then((response) => response.json())
        .then((responseJson) => {
            console.log(responseJson);
            dealsList=responseJson.data;
        })
        .catch((error) => {
            console.error(error);
        });
        console.log(dealsList)

        let data=[];
        for(let i=0;i<dealsList.length;i++){
            // API call to fetch deals
            await fetch('https://ido-api-services.herokuapp.com/get-single-deal/'+dealsList[i]+'?filter={"offset": 0,"limit": 100,"skip": 0,"order": "string","fields": {"Name": true,"id": true,"Ticker": true,"TotalRaize": true,"Currency": true,"Start": true,"End": true,"Price": true,"Vesting": true,"DealRate": true,"AllocationForTier1": true,"AllocationForTier2": true,"AllocationForTier3": true,"AllocationForTier4": true,"Description": true,"Icon": true,"CurrentRaize": true,"WalletAddress": true,"VisibleInAll": true,"VisibleInTop10": true,"VisibleInTier1": true,"VisibleInTier2": true,"VisibleInTier3": true,"VisibleInTier4": true}}').then(res=>res.json()).then(
                (result)=>{
                    console.log(result)
                    if(result.success){
                        data.push(result.res);
                    }
                }
            )
        }
        console.log(data);
        setDealsData(data);
        setLoading(false);
    }

    useEffect(()=>{
        setLoading(true)
        getDeals();
        // console.log(JSON.stringify(dealsData));
    },[walletData.walletAddress]);



return(<>

<div className="dashboard">

<div className="content">

<Menubar></Menubar>

<div className="right-content">
<div className="heading-row unlock-btn">

<h1>My Deals</h1>
<div className="connect-wallet">
    {/* Connect Wallet Button */}
    <ConnectWallet walletData={walletData} setWalletData={setWalletData}></ConnectWallet>
</div>
</div>

    {/* Options */}
    <div className="tab-row">
    </div>
    {/* tab row closed */}

    {/* Deals Cards */}
    <div className="tab-content">
        <Row>
        {/* Loading bar */}
        {loading?
        (
            <div>
                <Spinner animation="border" role="status">
                    <span className="visually-hidden">Loading...</span>
                </Spinner>
            </div>
        ):(<>
        {/* For Deals set the data */}
        {dealsData.map((data, key) => {
            return (
                    <Col md={4}>
                        <div className="tab-col">
                            <div className="user-name-row">
                                <img src={"data:image/png;base64,"+data.Icon}/>
                                <p>{data.Name}&emsp;<span>{data.Ticker}</span> </p>
                            </div>

                            <table style={{borderCollapse:'separate',marginTop:'10px'}}>
                                <tr>
                                    <td><span className='text-heading'>Total Raize:</span></td>
                                    <td><p className="amount">{data.TotalRaize} {data.Currency}</p></td>
                                </tr>
                                <tr>
                                    <td><span className='text-heading'>Max Allocaion:</span></td>
                                    <td><p className="amount">{data.AllocationForTier1} {data.Currency}</p></td>
                                </tr>
                                <tr>
                                    <td><span className='text-heading'>Min Allocation:</span></td>
                                    <td><p className="amount">{data.AllocationForTier4} {data.Currency}</p></td>
                                </tr>
                            </table>

                            {isFinished(data.End)?(
                                <div className="finished-row finished">
                                    <span className="icon"><img  src={"data:image/png;base64,"+data.Icon}/></span>
                                    <Button  className="button-strict"><span></span> Finished </Button>
                                </div>
                            ):(
                                <div className="finished-row">
                                    <span className="icon"><img  src={"data:image/png;base64,"+data.Icon}/></span>
                                    <Button  className="button-strict"><span></span>
                                        {getDealStauts(data.Start,data.End)}
                                    </Button>
                                </div>
                            )}
                            
                            <div className="finished-amt">
                                <p>{data.CurrentRaize/data.TotalRaize * 100}%</p>  <p className="amt">{data.CurrentRaize}/{data.TotalRaize}</p> 
                            </div>

                            <div className="button-row">
                            {isFinished(data.End)?(
                                <Button className="button-strict" onClick={e=>history.push('/contribute/'+data.id)}>View Deal</Button>
                            ):(
                                <Button className="button-strict" onClick={e=>history.push('/contribute/'+data.id)}>Join Deal</Button>
                            )}
                            </div>

                        </div>
                    </Col>
                );
        })}
        </>
        )}
        </Row>  
    </div>

    </div>
    </div>
    </div>
</>)
}

export default MyDeals