import React, { useState, useEffect, useContext } from "react";
import {Container, Row, Col, Button,Spinner,Modal} from "react-bootstrap";
import { toast } from 'react-toastify'; //toster
import './Style.css';
//connect wallet button
import ConnectWallet from "../components/Wallet/connectWallet";
import {WalletContext} from './WalletContext';
import Menubar from "../components/pageComponents/navigationBar";
// Route to contribute page 
import { useHistory } from "react-router-dom";
import RegistrationModel from "../components/pageComponents/registrationModel";
  

const Profile = ()=>{  
    const [registered,setRegistered] = useState(false);//user is registered
    const [editData,setEditData] = useState(false);//editing email and telegram data
    const [userEmail,setUserEmail] = useState('-');//user email
    const [userTelegram,setUserTelegram] = useState('-');//user telegram handle
    
    const [modalShow, setModalShow] = React.useState(false);
    //import hooks for object
    const [walletData, setWalletData] =  useContext(WalletContext)

    // jump to contribute page
    const history = useHistory()

    const getDetails= async ()=>{
        // API call to fetch deals
        var parameters={
            "walletAddress":walletData.walletAddress
        }
        console.log(parameters)
        fetch('https://ido-api-services.herokuapp.com/get-one', {
            method: 'post',
            headers: { 'Content-Type': 'application/json'
                },
            body: JSON.stringify(
                    parameters
                ),
            }).then((response) => response.json())
            .then((responseJson) => {
            console.log(responseJson);
            if(responseJson.success){
                setUserEmail(responseJson.data.Email);
                setUserTelegram(responseJson.data.TelegramHandle);
                setRegistered(true)
            }
            else
                toast.error(responseJson.error.message)
            })
            .catch((error) => {
            console.error(error);
            });
    }

    useEffect(()=>{
        if(walletData.isConnected)
            getDetails();
    },[walletData.isConnected])

    const editButtonCall=()=>{
        if(registered){ //already registered
            if(editData){
                setEditData(false)
            } else {
                setEditData(true)
                document.getElementById("email").value=userEmail;
                document.getElementById("telegramId").value=userTelegram;
            }
        } else{ //register 
            setModalShow(true)
        }
    }

    const saveButtonCall= async ()=>{
        // update to server
        //API call to server
        let userData={
            "WalletAddress":walletData.walletAddress,
            "Email":document.getElementById("email").value,
            "TelegramHandle":document.getElementById("telegramId").value
        }
        await fetch('https://ido-api-services.herokuapp.com/create-users', {
            method: 'post',
            headers: { 'Content-Type': 'application/json'
                },
            body: JSON.stringify(
                    userData
                ),
            }).then((response) => response.json())
            .then((responseJson) => {
            console.log(responseJson);
            if(responseJson.success)
                toast.success(responseJson.message)
            else
                toast.error(responseJson.error.message)
            })
            .catch((error) => {
            console.error(error);
            });
        setUserEmail(userData.Email);
        setUserTelegram(userData.TelegramHandle);
        setEditData(false)
    }


return(<>
<div className="dashboard">

<div className="content">

<Menubar></Menubar>

<div className="right-content">
<div className="heading-row unlock-btn">
<h1>Profile</h1>
<div className="connect-wallet">
    {/* Connect Wallet Button */}
    <ConnectWallet walletData={walletData} setWalletData={setWalletData}></ConnectWallet>
</div>
</div>

<div class="row-box">
    <span class="text-heading">Email</span>
    <input type="email" id="email" hidden={!editData} required></input>
    <span class="amount" id="emailValue" hidden={editData}>{userEmail}</span>
</div>
<div class="row-box">
    <span class="text-heading">Telegram Handle</span>
    <input type="text" id="telegramId" hidden={!editData} required></input>
    <span class="amount" id="telegramIdValue" hidden={editData}>{userTelegram}</span>
</div>

<div className="button-row">
    <button className="btn" onClick={editButtonCall}>
        {registered?
        (editData?'Cancel':
        'Change')
        :'Register'}
    </button>
{editData?<button className="btn" onClick={saveButtonCall}>Save</button>:<></>}
</div>

<>
      <RegistrationModel walletAddress={walletData.walletAddress}
        show={modalShow}
        onHide={() => setModalShow(false)}
      />
    </>
<div className="profile-content">
<div class="box-row row">
<div className="col-4"><div className="box-cnt"><h4>More user stats to come soon</h4></div></div>
<div className="col-4"><div className="box-cnt"><h4>More user stats to come soon</h4></div></div>
<div className="col-4"><div className="box-cnt"><h4>More user stats to come soon</h4></div></div>
<div className="col-4"><div className="box-cnt"><h4>More user stats to come soon</h4></div></div>
<div className="col-4"><div className="box-cnt"><h4>More user stats to come soon</h4></div></div>
<div className="col-4"><div className="box-cnt"><h4>More user stats to come soon</h4></div></div>
</div> {/*box row closed */}
</div>{/* tab content closed */}


</div> {/* right content closed   */}

</div> {/*content */}




</div>
</>)
}

export default Profile