import React, {useContext} from "react";
import {Button,Modal, Row, Col, Form} from "react-bootstrap";
import { toast } from 'react-toastify'; //toster


function RegistrationModel(props) {

  const handleSubmit = (event) => {
    event.preventDefault();
    // console.log(event.target)
    var formData={
        "WalletAddress":props.walletAddress,
        "Email": event.target.email.value,
        "TelegramHandle": event.target.telegramId.value
    }
    console.log(formData)
    fetch('https://ido-api-services.herokuapp.com/create-users', {
        method: 'post',
        headers: { 'Content-Type': 'application/json'
            },
        body: JSON.stringify(
                formData
            ),
        }).then((response) => response.json())
        .then((responseJson) => {
        console.log(responseJson);
        if(responseJson.success){
            toast.success(responseJson.message)
            window.location.reload();
        } else
            toast.error(responseJson.error.message)
        })
        .catch((error) => {
        console.error(error);
        });
  }


    return (
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header>
          <Modal.Title id="contained-modal-title-vcenter">
            Registration
          </Modal.Title>
            <button type="button" style={{backgroundColor:'white'}} onClick={props.onHide}><span style={{fontSize:'25px'}}></span></button>
        </Modal.Header>
        <Modal.Body>
          <div style={{textAlign:'center'}}>
            <p>
              To participate in our deals you need to complete registration by providing the following information:
            </p>
          </div>
          
          <Form className="deal-form" onSubmit={handleSubmit}>

          <Row>
          <Form.Group as={Row} className="mb-3" controlId="email">
            <Col sm="4">
              <Form.Label inline for="email">Email</Form.Label>
            </Col>
            <Col sm="8">
              <Form.Control inline type="email" id="email" placeholder="" required/>
            </Col>
          </Form.Group>  
          </Row>
          <Row>
          <Form.Group as={Row} className="mb-3" controlId="telegramId">
            <Col sm="4">
              <Form.Label inline for="telegramId">Telegram Handle</Form.Label>
            </Col>
            <Col sm="8">
              <Form.Control inline type="text" id="telegramId" placeholder="" required/>
              <Form.Text id="telegramIdHelpBlock" muted>
                  e.g. @JhonDoe
                </Form.Text>
            </Col>
          </Form.Group>  
          </Row>
                    
          <Row>
          <Col>
          <Button type="submit" style={{backgroundColor:'green'}}>SUBMIT</Button> 
          </Col>
          </Row>
          </Form>

        </Modal.Body>
        <Modal.Footer>
          {/* <Button onClick={props.onHide}>Close</Button> */}
          <div style={{textAlign:'center'}}>
            <p>
            Note: This information will be associated with the wallet [{props.walletAddress}]. You can change this at any time in the My Profile section.
            </p>
          </div>
        </Modal.Footer>
      </Modal>
    );
  }

  export default RegistrationModel