import React, { useState, useEffect, useContext } from "react";
import {Container, Row, Col, Button, ProgressBar, Spinner, OverlayTrigger, Tooltip} from "react-bootstrap";
import { toast } from 'react-toastify'; //toster
import 'react-toastify/dist/ReactToastify.css';
import { useParams,useHistory } from "react-router-dom";
import './Style.css';
//utils function 
import { isActive,getDealGoesLiveIn, getDealEndIn } from "./utils";
import Menubar from "../components/pageComponents/navigationBar";
//connect wallet button
import ConnectWallet from "../components/Wallet/connectWallet";
//contribute function call
import { contributeETH } from "../components/contracts/ETHContributeCAll";
import {contributeBSC} from "../components/contracts/BSCContributeCAll";
import {WalletContext} from './WalletContext';
import backOption from '../components/assects/chevron-left.svg';
import infoOption from '../components/assects/info-icon.svg';
import RegistrationModel from "../components/pageComponents/registrationModel";

const Contribute = ()=>{  
    const { id } = useParams();
    const [disableContribute,setDisableContribute] = useState(false);//Loading bar
    const [dealsDetails,setDealsDetails] = useState([{}]);//Deatils of deal
    const [dealStartTime,seDealStartTime] = useState('');//dealStartTime
    const [dealEndTime,setDealEndTime] = useState('');//dealEndTime
    const [yourContriution,setYourContriution] = useState(0);//dealEndTime
    const [loading,setLoading] = useState(false);//Loading bar
    const [isRegistered,setRegistered] = useState(false);//user register
    const [manageSelltoggle,setManageSelltoggle] = useState(false);//user Manage Sell Toggle
    const [modalShow, setModalShow] = React.useState(false);
    const history = useHistory()

    //import hooks for object
    const [walletData, setWalletData] =  useContext(WalletContext)

    const getDetails= async ()=>{
        setLoading(true)
        // API call to fetch deals
        await fetch('https://ido-api-services.herokuapp.com/get-single-deal/'+id+'?filter={"offset": 0,"limit": 100,"skip": 0,"order": "string","fields": {"Name": true,"id": true,"Ticker": true,"TotalRaize": true,"Currency": true,"Start": true,"End": true,"Price": true,"Vesting": true,"DealRate": true,"AllocationForTier1": true,"AllocationForTier2": true,"AllocationForTier3": true,"AllocationForTier4": true,"Description": true,"Icon": true,"CurrentRaize": true,"WalletAddress": true,"ManageSell": true}}').then(res=>res.json()).then(
          (result)=>{
            // console.log(result.error)
                if(result.success){
                    //set return data into dealsDetails object
                    setDealsDetails(result.res);
                    console.log(result.res)
                    setLoading(false)
                } else {
                    if(result.error.statusCode==404){
                        toast.error("Deal not found !"); 
                        history.push('/')   
                    } else{
                        console.log('Error while loading deal!');
                        toast.error("Error while loading deal!");    
                    }
                    setLoading(false)
                }
            }
        )
    }

    useEffect(()=>{
        setLoading(true)
        if(dealsDetails.CurrentRaize<dealsDetails.TotalRaize){
            //set times
            setInterval(()=>{
                let result=getDealGoesLiveIn(dealsDetails.Start)
                if(result)
                    seDealStartTime(result)
                },1000);
            setInterval(()=>{
                let result=getDealEndIn(dealsDetails.End)
                if(result)
                    setDealEndTime(result)
            },1000);
            if(isActive(dealsDetails.Start,dealsDetails.End))
                setDisableContribute(false)
            else
                setDisableContribute(true)
        } else{
            seDealStartTime('Closed')
            setDealEndTime('Closed')    
            setDisableContribute(true)
        }
        setLoading(false)
    },[dealsDetails]);

    useEffect(()=>{
        getDetails();
    },[]);

    useEffect(()=>{
        // API call to fetch user contribution
        if(walletData.walletAddress.length==0){
            return
        }
        let contributeData={
            dealId: id,
            userWalletAddress: walletData.walletAddress
        }
        // console.log(JSON.stringify(contributeData));
        //API call for check data is present
        fetch('https://ido-api-services.herokuapp.com/get-contributions', {
            method: 'post',
            headers: { 'Content-Type': 'application/json' },
            cache:"no-cache",
            body:  JSON.stringify(
                    contributeData
                ),
            }).then((response) => response.json())
            .then((responseJson) => {
            // console.log(responseJson);
            //already  present 
            if(responseJson.success){
                // set Value
                setYourContriution(responseJson.data.TotalAmountInvested)
            } else{
                setYourContriution(0)
                }
            })
            .catch((error) => {
            console.error(error);
            });
            //API call for check user is registered
            let userData={
                walletAddress:walletData.walletAddress
            }
            fetch('https://ido-api-services.herokuapp.com/get-one', {
                method: 'post',
                headers: { 'Content-Type': 'application/json' },
                cache:"no-cache",
                body:  JSON.stringify(
                        userData
                    ),
                }).then((response) => response.json())
                .then((responseJson) => {
                // console.log(responseJson);
                //already registered set value 
                setRegistered(responseJson.success)
                })
                .catch((error) => {
                console.error(error);
                });
        
        //API call for check Manage Sell is present
        let manageSellData={
            walletAddress: walletData.walletAddress,
            dealId: id
        }
        fetch('https://ido-api-services.herokuapp.com/manage-sells', {
            method: 'post',
            headers: { 'Content-Type': 'application/json' },
            cache:"no-cache",
            body:  JSON.stringify(
                    manageSellData
                ),
            }).then((response) => response.json())
            .then((responseJson) => {
            // console.log(responseJson);
            // console.log(responseJson.data.length);
            //already  present 
            if(responseJson.data.length > 0){
                // set Value
                setManageSelltoggle(true)
            } else{
                setManageSelltoggle(false)
                }
            })
            .catch((error) => {
            console.error(error);
            });
    },[walletData.walletAddress]);

    useEffect(()=>{
        //set input contribute field
        setContributionToMax();
        document.getElementById('contributionAmount').max=getYourMaxAllocaion(walletData.tier);
    },[walletData.tier,yourContriution]);

    const setContributionToMax= ()=>{
        //set input contribute field
        document.getElementById('contributionAmount').value=getYourMaxAllocaion(walletData.tier);
    }

    //Contribute Button call
    const contribute= async ()=>{
        setLoading(true)
        // Smart contract call to fetch deals
        if(walletData.isConnected){
            //if tier 0 then ask to stack token first
            if(walletData.tier==0){
                toast.error("You don't have tokens in stake")
            } else{
                if(isRegistered){ //is email and telegram present 
                    let value = document.getElementById('contributionAmount').value;
                    if(value<=getYourMaxAllocaion(walletData.tier) && value>0){
                        // alert("contribute button click")
                        var sucessCall=false;
                        let valueUSDT=walletData.web3.utils.toWei(value,'ether'); //convert to USDT with proper decimal value
                        //Contract call
                        if(walletData.networkName=="Eth"){
                            //call ethereum smart contract
                            sucessCall = await contributeETH(walletData.walletAddress,valueUSDT,dealsDetails.WalletAddress)
                        } else if(walletData.networkName=="BSC"){
                            //call BSC smart contract
                            sucessCall = await contributeBSC(walletData.walletAddress,valueUSDT,dealsDetails.WalletAddress)
                        }
                        if(sucessCall){ //if smart contract updated then only
                            //API Call to update contributed amount
                            dealsDetails.CurrentRaize=parseFloat(dealsDetails.CurrentRaize)+parseFloat(value);
                            let jsonData={
                                "CurrentRaize":dealsDetails.CurrentRaize,
                                "id":id
                            }
                            // setDealsDetails(jsonData) //for Test data 
                            // console.log(jsonData)
                            toast.success('Contributed Successfully!')
                            await fetch('https://ido-api-services.herokuapp.com/update-currentRaize', {
                                method: 'post',
                                headers: { 'Content-Type': 'application/json' },
                                body: JSON.stringify(
                                        jsonData
                                    ),
                                }).then((response) => response.json())
                                .then((responseJson) => {
                                console.log(responseJson);
                                })
                                .catch((error) => {
                                console.error(error);
                                });
                                //Set Your Contriution 
                                //API call for update
                                let contributeData={
                                    DealId: id,
                                    UserWalletAddress: walletData.walletAddress,
                                    TotalAmountInvested: parseFloat(yourContriution)+parseFloat(value)
                                }
                                // console.log(contributeData)
                                // create call
                            await fetch('https://ido-api-services.herokuapp.com/create-contributors', {
                                    method: 'post',
                                    headers: { 'Content-Type': 'application/json' },
                                    body:  JSON.stringify(
                                            contributeData
                                            ),
                                    }).then((response) => response.json())
                                    .then((responseJson) => {
                                        console.log(responseJson);
                                    })
                                    .catch((error) => {
                                        console.error(error);
                                    });
                                    //Set Your Contriution 
                                    setYourContriution(parseFloat(yourContriution)+parseFloat(value))
                        } else{
                            console.log("Error while contract call! Please try again!")
                            toast.error("Error while contract call! Please try again!")
                        }
                    } else{
                        console.log("Value should be less than max Allocation and more than 0 !")
                        toast.error("Value should be less than max Allocation and more than 0 !")
                    }    
                } else {
                    // register popup 
                    setModalShow(true)
                }
            }
        } else{
            console.log('Please connect your wallet first!')
            toast.error("Please connect your wallet first!")
        }
        //uppdate deal details
        //set Loading false
        setLoading(false)
    }

    const getYourMaxAllocaion= (tier)=>{
        let result=0;
        //Set according to its tier
        switch(tier){
            case 1: result=dealsDetails.AllocationForTier1;
                    break;
            case 2: result=dealsDetails.AllocationForTier2;
                    break;
            case 3: result=dealsDetails.AllocationForTier3;
                    break;
            case 4: result=dealsDetails.AllocationForTier4;
                    break;
            default: result=0;
        }
        //Check for remaning tokens
        let remaning=parseFloat(dealsDetails.TotalRaize) - parseFloat(dealsDetails.CurrentRaize);
        if(remaning<result)
            result=remaning;
        result=result-yourContriution;
        if(result<0)
            return 0;
        return result;
    }
    
    const ManageSell=()=>{
        if(manageSelltoggle){
        //Delete ManageSell record API Call
        let manageSellData={
            walletAddress: walletData.walletAddress,
            dealId: id
        }
        fetch('https://ido-api-services.herokuapp.com/manage-sells-delete', {
            method: 'post',
            headers: { 'Content-Type': 'application/json' },
            cache:"no-cache",
            body:  JSON.stringify(
                    manageSellData
                ),
            }).then((response) => response.json())
            .then((responseJson) => {
            // console.log(responseJson);
            //deleted Successfully 
                if(responseJson.success){
                    setManageSelltoggle(false)
                }
            })
            .catch((error) => {
            console.error(error);
            });
        }else{
        // Add data to Manage Sell   
        let manageSellData={
            WalletAddress: walletData.walletAddress,
            DealId: id
        }
        fetch('https://ido-api-services.herokuapp.com/create-manage-sells', {
            method: 'post',
            headers: { 'Content-Type': 'application/json' },
            cache:"no-cache",
            body:  JSON.stringify(
                    manageSellData
                ),
            }).then((response) => response.json())
            .then((responseJson) => {
            // console.log(responseJson);
            //created Successfully 
                if(responseJson.id){
                    setManageSelltoggle(true)
                }
            })
            .catch((error) => {
            console.error(error);
            });
        }
    }

    return(<>

<div className={loading ? 'parentDisable' : ''} width="100%">
    <div className={loading ? 'overlay-box' : 'hide'}>
        <Spinner animation="border" role="status">
            <span className="visually-hidden">Loading...</span>
        </Spinner>
    </div>



<div className="dashboard">

<div className="content">

<Menubar></Menubar>

<div className="right-content">
<div>
<h1><a className="previous" onClick={() => history.push('/')}><img src={backOption}></img></a> <label className="page-heading"> Deal Details</label>
</h1>
</div>

    <>
      <RegistrationModel walletAddress={walletData.walletAddress}
        show={modalShow}
        onHide={() => setModalShow(false)}
      />
    </>

{/* Deals Cards */}
<div className="tab-content">
<Row>

<Col md={4}> 
<div className="user-name-row dl-dt">
<img src={"data:image/png;base64,"+dealsDetails.Icon}/>
<p>{dealsDetails.Name} <span>{dealsDetails.Ticker}</span> </p>
</div>
</Col>

<Col md={4}>
<div class="row-box"><p class="text-heading">Deal goes live in</p><p class="amount">{dealStartTime}</p></div>
<div class="row-box dl-dt"><p class="text-heading">Deal ends in</p><p class="amount">{dealEndTime}</p></div>
</Col>

<Col md={4}>
<div className="connect-wallet">
    {/* Connect Wallet Button */}
    <ConnectWallet walletData={walletData} setWalletData={setWalletData}></ConnectWallet>
</div>

</Col>
</Row>

<Row className="deal-row">
<Col md={4}>

</Col>

<Col md={8}>

</Col>
</Row>

<Row className="deal-row">
<Col md={12}>
<div className="tab-col">
<div className="heading-sm">Deal Progress</div> 
<Row className="total-box">
<Col md={4}><div class="row-box mt-0"><p class="text-heading">Total Raize</p><p class="amount">{dealsDetails.TotalRaize?dealsDetails.TotalRaize.toLocaleString():dealsDetails.TotalRaize}</p></div></Col>  
<Col md={4}><div class="row-box mt-0"><p class="text-heading">Total Amount Contributed</p><p class="amount">{dealsDetails.CurrentRaize?dealsDetails.CurrentRaize.toLocaleString():dealsDetails.CurrentRaize}</p></div></Col> 
<Col md={4}>
<div class="row-box mt-0"><p class="text-heading">Rate</p><p class="amount">{dealsDetails.DealRate}</p></div>

</Col>  
</Row>  
<div className="progress-bar-row">
<ProgressBar now={(dealsDetails.CurrentRaize/dealsDetails.TotalRaize)*100} />
</div> 
<div className="finished-amt">
<p>{dealsDetails.CurrentRaize/dealsDetails.TotalRaize * 100}%</p>  <p className="amt">{dealsDetails.CurrentRaize}/{dealsDetails.TotalRaize}</p> 
</div>
</div>
</Col>
</Row>


<Row className="deal-row">
<Col md={3}>
    <input type="number" id="contributionAmount" min="0" step="0.5"></input>
    <Button className="contributionAmount-maxButton" onClick={setContributionToMax}>Max</Button>
    <Button className="cnt-button button-strict" onClick={contribute} disabled={disableContribute}> Contribute </Button>
    
    <div hidden={!(dealsDetails.ManageSell && yourContriution>0)}>
        {/* !isRegistered */}
    <Button className="button-strict" style={{width:'90%',marginTop:'5px'}}
            onClick={ManageSell}>
            {manageSelltoggle?'Cancel Managed Sale ':'Request Managed Sale '}  </Button>
    <OverlayTrigger placement="bottom" overlay={
        <Tooltip id="button-tooltip-2">
            The Managed Sell option is intended to save on high tx fees on the Ethereum blockchain. By activating the option you choose to delegate the token selling to the Platform administrators (the selling timing is chosen to their own discretion) and to receive the selling proceeds in the form of stable coins on the BSC blockchain.
        </Tooltip>
    }>
        <img src={infoOption} style={{width:'8%',margin:'6px 0px 0px 5px'}}></img>
    </OverlayTrigger>
    </div>
    <label hidden={yourContriution<=0} style={{width:'100%',textAlign:'center',marginTop:'5px'}}>{manageSelltoggle?'Managed Sell Activated':'Managed Sell Deactivated'}</label>
</Col>

<Col md={9}>
<div className="tab-col">
 
<Row className="total-box">
<Col md={2}>
<div className="row-box">
<p className="text-heading">Your Tier: </p> 
<p className="amount">Tier {walletData.tier}</p>  
</div> 
</Col> 
<Col md={6}>
<table style={{borderCollapse:'separate'}}>
    <tr>
        <td><span className='text-heading'>Token locked(BSC):</span></td>
        <td><p className="amount">{walletData.BSCTokenCount.toLocaleString()} BULK</p></td>
    </tr>
    <tr>
        <td><span className='text-heading'>Token locked(ETH):</span></td>
        <td><p className="amount">{walletData.ETHTokenCount.toLocaleString()} BULK</p></td>
    </tr>
    <tr>
        <td><span className='text-heading'>Total:</span></td>
        <td><p className="amount">{walletData.TotalTokenCount.toLocaleString()} BULK</p></td>
    </tr>
</table>
</Col> 

<Col md={4}>
<table style={{borderCollapse:'separate'}}>
    <tr>
        <td>
            <span className='text-heading'>Your Max Allocaion:</span>
            <p class="text-heading">(Amount in {dealsDetails.Currency})</p>
        </td>
        <td style={{verticalAlign:'inherit'}}><p className="amount">{getYourMaxAllocaion(walletData.tier)}</p></td>
    </tr>
    <tr>
        <td>
            <span className='text-heading'>Your Total Contribution:</span>
            <p class="text-heading">(Amount in {dealsDetails.Currency})</p>
        </td>
        <td style={{verticalAlign:'inherit'}}><p className="amount">{yourContriution}</p></td>
    </tr>
</table>  
</Col>

</Row>  
</div>
</Col>
</Row>



<div className="tab-col">
<Row>
<Col md={4}>
<div class="deal-info">
<div className="heading-sm">Deal Info</div> 
<table style={{borderCollapse:'separate'}}>
    <tr>
        <td><span className='text-heading'>Start:</span></td>
        <td>{new Date(dealsDetails.Start).toUTCString()}</td>
    </tr>
    <tr>
        <td><span className='text-heading'>End:</span></td>
        <td> {new Date(dealsDetails.End).toUTCString()}</td>
    </tr>
    <tr>
        <td><span className='text-heading'>Currency:</span></td>
        <td>{dealsDetails.Currency}</td>
    </tr>
    <tr>
        <td><span className='text-heading'>Price:</span></td>
        <td>{dealsDetails.Price}</td>
    </tr>
    <tr>
        <td style={{verticalAlign:'baseline'}}><span className='text-heading'>Vesting:</span></td>
        <td>{dealsDetails.Vesting}</td>
    </tr>
</table>
</div> 
</Col> 

<Col md={5}>
<div class="deal-info">
<div className="heading-sm">Allocation per contributor</div>
<ul>
<li><span className='text-heading'>Tier 1:</span> {dealsDetails.AllocationForTier1} {dealsDetails.Currency}</li>
<li><span className='text-heading'>Tier 2:</span> {dealsDetails.AllocationForTier2} {dealsDetails.Currency}</li>  
<li><span className='text-heading'>Tier 3:</span> {dealsDetails.AllocationForTier3} {dealsDetails.Currency}</li>  
<li><span className='text-heading'>Tier 4:</span> {dealsDetails.AllocationForTier4} {dealsDetails.Currency}</li>    
</ul>
</div> 
</Col>  

<Col md={3} className="buy-div">
<div className="col-100">
    <a href="https://app.uniswap.org/#/swap?outputCurrency=0xa143ac515dca260a46c742c7251ef3b268639593" target="_blank">
        <Button className="uniswap"> Buy on Uniswap </Button>
    </a>
</div>
<div className="col-100%">  
    <a href="https://pancakeswap.finance/swap?outputCurrency=0xa143ac515dca260a46c742c7251ef3b268639593" target="_blank">
        <Button className="pancakeswap">Buy on Pancakeswap</Button>
    </a>
</div>  
</Col>

</Row> 
</div>

<div className="tab-col">

<Row className="descr-btn">
<Col md={12}>
<div dangerouslySetInnerHTML={{__html: dealsDetails.Description}}>
</div>  
</Col>
</Row>

</div>
</div>

</div> 
</div>
</div> 
</div>
    </>)
}

export default Contribute