import React, { useState, useEffect, useContext } from "react";
import {Container, Row, Col, Button,Spinner} from "react-bootstrap";
import { toast } from 'react-toastify'; //toster
import './Style.css';
// Route to contribute page 
import { useParams, useHistory } from "react-router-dom";
//utils function 
import { isFinished, isActive, isUpcoming, getDealStauts } from "./utils";
//connect wallet button
import ConnectWallet from "../components/Wallet/connectWallet";
import {WalletContext} from './WalletContext';
import Menubar from "../components/pageComponents/navigationBar";
import NoAccessModel from "../components/pageComponents/noAccessModel";

const MemberArea = ()=>{  
    const { type } = useParams();
    const [dealsData,setDealsData] = useState([{}]);
    const [tabIndex,setTabIndex] = useState(0);
    const [loading,setLoading] = useState(false);//Loading bar
    
    const [modalShow, setModalShow] = React.useState(false);
    //import hooks for object
    const [walletData, setWalletData] =  useContext(WalletContext)
    // jump to contribute page
    const history = useHistory()

    const getDeals = async ()=>{
        // Select filter
        let filter='';
        switch(type){
            case 'top10':filter='VisibleInTop10'
                break
            case 'tier1':filter='VisibleInTier1'
                break
            case 'tier2':filter='VisibleInTier2'
                break
            case 'tier3':filter='VisibleInTier3'
                break
            case 'tier4':filter='VisibleInTier4'
                break
        }
        // API call to fetch deals
        await fetch('https://ido-api-services.herokuapp.com/get-all-deals?filter={"offset": 0,"limit": 100,"skip": 0,"order": "string","where": {"additionalProp1": {}},"fields": {"Name": true,"id": true,"Ticker": true,"TotalRaize": true,"Currency": true,"Start": true,"End": true,"AllocationForTier1": true,"AllocationForTier4": true,"Description": true,"Icon": true,"CurrentRaize": true,"'+filter+'": true}}').then(res=>res.json()).then(
          (result)=>{
            console.log(result)
            let data=[];
                if(result.success){
                    if(tabIndex==0){ //set all deals
                        result.res.forEach((item) => {
                            if(item[filter]){
                                data.push(item);
                            }
                            // console.log(item[filter])
                        });
                    }else{
                        if(tabIndex==1){ //set active deals only
                            result.res.forEach((item) => {
                                if(isActive(item.Start,item.End) && item[filter]){
                                    data.push(item);
                                }
                            }); 
                        } else if(tabIndex==2){ //set upcoming deals only
                            result.res.forEach((item) => {
                                if(isUpcoming(item.Start) && item[filter]){
                                    data.push(item);
                                }
                            }); 
                        } else if(tabIndex==3){ //set finished deals only
                            result.res.forEach((item) => {
                                if(isFinished(item.End) && item[filter]){
                                    data.push(item);
                                }
                            }); 
                        }
                    }
                    setDealsData(data);
                    setLoading(false)
                }else{
                    toast.error('Error while loading deals!');
                }
            }
        )
    }

    const handleDealType= async (index)=>{
        // Handle tab selected
        if(index!==tabIndex){
            setLoading(true)
            setTabIndex(index);
        }
    }

    const isTop10= async()=>{
        let result=false;
        await fetch('https://bulkstaking.ew.r.appspot.com/process').then(res=>res.json()).then(
          (result)=>{
            // console.log(result.tops);
            result.tops.forEach(function (item, index) {
                if(walletData.walletAddress==item){
                    console.log(item);
                    result=true;
                }
            });
          });
        if(result){
            getDeals();
        } else{
            setModalShow(true)
            setDealsData([]);
            setLoading(false)
        }
    }

    useEffect(()=>{
        setLoading(true)
        
        let filter=0;
        switch(type){
            case 'tier1':filter=1
                break
            case 'tier2':filter=2
                break
            case 'tier3':filter=3
                break
            case 'tier4':filter=4
                break
        }
        //wallet data
        if(!walletData.isConnected){
            toast.error('Please Connect wallet first!')
        } else if(type=='top10'){
            isTop10();
        } else if(walletData.tier!=filter){
            // toast.error('Unauthorised for access!')
            setModalShow(true)
            setDealsData([]);
            setLoading(false)
        } else{
            getDeals();
        }
        // console.log(JSON.stringify(dealsData));
    },[tabIndex,type]);

    const getPageHeading=()=>{
        let heading='';
        switch(type){
            case 'top10':heading="Top 10"
                break
            case 'tier1':heading="Tier 1"
                break
            case 'tier2':heading="Tier 2"
                break
            case 'tier3':heading="Tier 3"
                break
            case 'tier4':heading="Tier 4"
                break
        }
        return heading
    }

return(<>


<div className="dashboard">

<div className="content">

<Menubar></Menubar>

<div className="right-content">
<div className="heading-row unlock-btn">

<h1>{getPageHeading()}</h1>
<div className="connect-wallet">
    {/* Connect Wallet Button */}
    <ConnectWallet walletData={walletData} setWalletData={setWalletData}></ConnectWallet>
</div>
</div>

<>
      <NoAccessModel
        show={modalShow}
        onHide={() => setModalShow(false)}
      />
    </>

    {/* Options */}
    <div className="tab-row">
        <a className={tabIndex==0?"active":""} onClick={() => handleDealType(0)}>All Deals</a>
        <a className={tabIndex==1?"active":""} onClick={() => handleDealType(1)}>Active</a>
        <a className={tabIndex==2?"active":""} onClick={() => handleDealType(2)}>Upcoming</a>
        <a className={tabIndex==3?"active":""} onClick={() => handleDealType(3)}>Finished</a>  
    </div>
    {/* tab row closed */}

    {/* Deals Cards */}
    <div className="tab-content">
        <Row>
        {/* Loading bar */}
        {loading?
        (
            <div>
                <Spinner animation="border" role="status">
                    <span className="visually-hidden">Loading...</span>
                </Spinner>
            </div>
        ):(<>
        {/* For Deals set the data */}
        {dealsData.map((data, key) => {
            return (
                    <Col md={4}>
                        <div className="tab-col">
                            <div className="user-name-row">
                                <img src={"data:image/png;base64,"+data.Icon}/>
                                <p>{data.Name}&emsp;<span>{data.Ticker}</span> </p>
                            </div>

                            <table style={{borderCollapse:'separate',marginTop:'10px'}}>
                                <tr>
                                    <td><span className='text-heading'>Total Raize:</span></td>
                                    <td><p className="amount">{data.TotalRaize} {data.Currency}</p></td>
                                </tr>
                                <tr>
                                    <td><span className='text-heading'>Max Allocaion:</span></td>
                                    <td><p className="amount">{data.AllocationForTier1} {data.Currency}</p></td>
                                </tr>
                                <tr>
                                    <td><span className='text-heading'>Min Allocation:</span></td>
                                    <td><p className="amount">{data.AllocationForTier4} {data.Currency}</p></td>
                                </tr>
                            </table>

                            {isFinished(data.End)?(
                                <div className="finished-row finished">
                                    <span className="icon"><img  src={"data:image/png;base64,"+data.Icon}/></span>
                                    <Button  className="button-strict"><span></span> Finished </Button>
                                </div>
                            ):(
                                <div className="finished-row">
                                    <span className="icon"><img  src={"data:image/png;base64,"+data.Icon}/></span>
                                    <Button  className="button-strict"><span></span>
                                        {getDealStauts(data.Start,data.End)}
                                    </Button>
                                </div>
                            )}
                            
                            <div className="finished-amt">
                                <p>{data.CurrentRaize/data.TotalRaize * 100}%</p>  <p className="amt">{data.CurrentRaize}/{data.TotalRaize}</p> 
                            </div>

                            <div className="button-row">
                            {isFinished(data.End)?(
                                <Button className="button-strict" onClick={e=>history.push('/contribute/'+data.id)}>View Deal</Button>
                            ):(
                                <Button className="button-strict" onClick={e=>history.push('/contribute/'+data.id)}>Join Deal</Button>
                            )}
                            </div>

                        </div>
                    </Col>
                );
        })}
        </>
        )}
        </Row>  
    </div>
</div>
</div>
</div>
</>)
}

export default MemberArea